document.addEventListener('DOMContentLoaded', () => {

    /* pages */
    require('./_pages/_main');
    require('./_pages/_product_list');
    require('./_pages/_product');
    require('./_pages/_stocks');
    require('./_pages/_enterprises');
    require('./_pages/_company');
    require('./_pages/_cart');

    if (window.File && window.FileList && window.FileReader) {
        $('.image-group input[type="file"]').on("change", function(e) {
            var fileGroup = $(this).parent(),
                files = e.target.files,
                filesLength = files.length;
            for (var i = 0; i < filesLength; i++) {
                var f = files[i]
                var fileReader = new FileReader();
                fileReader.onload = (function(e) {
                    var file = e.target;
                    $("<span class=\"pip\">" +
                        "<img class=\"imageThumb\" src=\"" + e.target.result + "\" title=\"" + file.name + "\"/>" +
                        "<span class=\"remove\"><i class='icon icon-cancel'></i></span>" +
                        "</span>").insertAfter(fileGroup);
                    $(".remove").click(function(){
                        $(this).parent(".pip").remove();
                    });

                    // Old code here
                    /*$("<img></img>", {
                      class: "imageThumb",
                      src: e.target.result,
                      title: file.name + " | Click to remove"
                    }).insertAfter("#files").click(function(){$(this).remove();});*/

                });
                fileReader.readAsDataURL(f);
            }
        });
    } else {
        alert("Your browser doesn't support to File API")
    }
    /* resize functions */
    $( window ).resize(function() {
        if($(window).width() >=1024) {
            $('.catalog__title').detach().prependTo('.catalog__content__right-side');
        }
        else{
            $('.catalog__title').detach().prependTo('.catalog__content');
        }
    });
    /* scroll to block */
    $("body").on('click', '.go_to', function(e){
        var fixed_offset = 65;
        $('html,body').stop().animate({ scrollTop: $(this.hash).offset().top - fixed_offset }, 1000);
        e.preventDefault();
    });
    /* scroll top TOP */
    $("body").on('click', '.toTop', function(e){
        $('html,body').stop().animate({ scrollTop: 0 }, 1000);
        e.preventDefault();
    });
    /* modals */
    $('.modal-win').magnificPopup({
        type: 'inline',
        fixedContentPos: true,
        fixedBgPos: true,
        closeBtnInside: false,
        midClick: true,
        removalDelay: 300,
        tClose: 'Закрыть',
        mainClass: 'my-mfp-zoom-in',
        image: {
            tError: 'Невозможно загрузить :('
        },
        ajax: {
            tError: 'Невозможно загрузить :('
        }
    });
});



